import { makeStylesWithIndex } from 'common/hooks'
import { useIntl } from 'react-intl'
import { footerMessages } from 'translations/messages'
import FooterBg from 'assets/img/bg_footer.png'
import { Box, useMediaQuery } from '@material-ui/core'
import { BREAKPOINTS } from 'common/constant'
import { RefObject } from 'react'
import AndroidIosImg from 'assets/img/android_ios.png'
import DownloadQRcode from 'assets/img/download_qrcode.png'

const useStyles = makeStylesWithIndex((theme) => ({
  footer: {
    position: 'relative',
    boxSizing: 'border-box',
    width: '100%',
    [theme.breakpoints.down(1200)]: {
      position: 'absolute',
      left: '0px',
      backgroundImage: `url("${FooterBg}")`,
      backgroundSize: 'cover',
      backgroundPosition: 'center',
    },
  },
  container: {
    position: 'absolute',
    top: 0,
    left: 0,
    width: '1440px',
    marginLeft: 'calc(50% - 720px)',
    paddingTop: '80px',
    '& .left-part': {
      marginRight: '159px',
    },
    [theme.breakpoints.down(BREAKPOINTS[1660])]: {
      width: '1320px',
      marginLeft: 'calc(50% - 660px)',
    },
    [theme.breakpoints.down(BREAKPOINTS[1380])]: {
      width: '1024px',
      marginLeft: 'calc(50% - 512px)',
      '& .left-part': {
        marginRight: '80px',
      },
    },
    [theme.breakpoints.down(1200)]: {
      position: 'relative',
      width: '100vw',
      marginLeft: '0px',
      padding: '50px 40px',
      '& .left-part': {
        marginRight: '50px',
      },
    },
    [theme.breakpoints.down(BREAKPOINTS[768])]: {
      padding: '15px',
      '& .left-part': {
        marginRight: '10px',
      },
    },
  },
  title: {
    color: '#E7C53B',
    fontSize: '48px',
    letterSpacing: '4px',
    [theme.breakpoints.down(BREAKPOINTS[1380])]: {
      fontSize: '24px',
      marginBottom: '18px',
    },
    [theme.breakpoints.down(BREAKPOINTS[768])]: {
      fontSize: '14px',
      marginBottom: '14px',
    },
  },
  content: {
    color: '#E0E0E0',
    fontSize: '24px',
    '&.left-content': {
      marginTop: '59px',
    },
    '&.right-content': {
      marginTop: '39px',
    },
    [theme.breakpoints.down(BREAKPOINTS[1380])]: {
      fontSize: '16px',
      '&.left-content': {
        marginTop: '29px',
      },
      '&.right-content': {
        marginTop: '19px',
      },
    },
    [theme.breakpoints.down(BREAKPOINTS[768])]: {
      fontSize: '12px',
      '& .android-ios': {
        width: '40px',
      },
    },
  },
}))

export default function Footer(props: { footerRef: RefObject<HTMLDivElement> }) {
  const classes = useStyles()
  const { formatMessage: format } = useIntl()
  const matched = useMediaQuery('(min-width:1200px)')

  return (
    <footer className={classes.footer} ref={props.footerRef}>
      {matched && <img src={FooterBg} alt="footer" style={{ width: '100%' }} />}
      <div className={classes.container}>
        <Box display="flex" alignItems="flex-start">
          <div className="left-part">
            <div className={classes.title}>{format(footerMessages.anywhere)}</div>
            <Box className={`${classes.content} left-content`} display="flex" alignItems="center">
              <img
                className="android-ios"
                src={AndroidIosImg}
                alt="android_ios"
                style={{ marginRight: '15px' }}
              />
              <span>{format(footerMessages.scanDownload)}</span>
            </Box>
          </div>

          <div className={classes.content}>
            <div className={classes.title}>{format(footerMessages.contactUs)}</div>
            <div className={`${classes.content} right-content`}>
              <div style={{ marginBottom: '41px' }}>Telegram：@123qwe</div>
              <div>Skype：12345678</div>
            </div>
          </div>
        </Box>
        <img src={DownloadQRcode} alt="download" />
      </div>
    </footer>
  )
}
