import { makeStyles, Theme as DefaultTheme } from '@material-ui/core'
import { Styles, WithStylesOptions } from '@material-ui/core/styles/withStyles'

// https://stackoverflow.com/questions/62473898/material-ui-rendering-bugs-in-production-build/62646041#62646041
// To avoid <style> injection order breaking our styles
// We need to specify makeStyles options "index" to force our custom styles to be injected
// at the end of the <head>
export function makeStylesWithIndex<
  Theme = DefaultTheme,
  Props extends object = {},
  ClassKey extends string = string
>(styles: Styles<Theme, Props, ClassKey>, options?: Omit<WithStylesOptions<Theme>, 'withTheme'>) {
  return makeStyles(styles, { index: 1, ...options })
}
