import { makeStylesWithIndex } from 'common/hooks'
import TwoImg from 'assets/img/2.png'
import ClubImg from 'assets/img/club.png'
import EntertainmentImg from 'assets/img/entertainment.png'
import GameImg from 'assets/img/game.png'
import SportImg from 'assets/img/sport.png'
import TicketImg from 'assets/img/ticket.png'
import { sectionTwoMessages } from 'translations/messages'
import { ArrowContent, ArrowTitle, SelectableCard } from './SectionBase'
import { useIntl } from 'react-intl'
import { Box } from '@material-ui/core'
import { useState } from 'react'

const useStyles = makeStylesWithIndex(() => ({
  sectionTwo: {
    marginTop: '57px',
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    position: 'relative',
  },
}))

export default function SectionTwo(props: { scale: number }) {
  const classes = useStyles()
  const { formatMessage: format } = useIntl()
  const [selectedIdx, setSelectedIdx] = useState(0)

  return (
    <section
      className={classes.sectionTwo}
      style={{
        transform: `scale(${props.scale})`,
      }}
    >
      <ArrowTitle title={format(sectionTwoMessages.title)} img={TwoImg} />
      <ArrowContent
        content={format(sectionTwoMessages.content)}
        img={ClubImg}
        floatingPosition="right"
      />
      <Box display="flex" justifyContent="space-around" width="100%" marginTop="96px">
        <SelectableCard
          img={EntertainmentImg}
          title={format(sectionTwoMessages.entertainment)}
          selected={selectedIdx === 0}
          onClick={() => setSelectedIdx(0)}
        />
        <SelectableCard
          img={GameImg}
          title={format(sectionTwoMessages.game)}
          selected={selectedIdx === 1}
          onClick={() => setSelectedIdx(1)}
        />
        <SelectableCard
          img={SportImg}
          title={format(sectionTwoMessages.sport)}
          selected={selectedIdx === 2}
          onClick={() => setSelectedIdx(2)}
        />
        <SelectableCard
          img={TicketImg}
          title={format(sectionTwoMessages.ticket)}
          selected={selectedIdx === 3}
          onClick={() => setSelectedIdx(3)}
        />
      </Box>
    </section>
  )
}
