import { Box, MenuItem, Select } from '@material-ui/core'
import { Locale } from 'common/types'
import { useIntl } from 'react-intl'
import { headerMessages } from 'translations/messages'
import LogoImg from 'assets/img/logo.png'
import { ReactComponent as ZhFlagIcon } from 'assets/icon/zh.svg'
import { ReactComponent as ThFlagIcon } from 'assets/icon/th.svg'
import { ReactComponent as UsFlagIcon } from 'assets/icon/us.svg'
import { ReactNode } from 'react'
import { makeStylesWithIndex } from 'common/hooks'
import { BREAKPOINTS } from 'common/constant'

const useStyles = makeStylesWithIndex((theme) => ({
  header: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    paddingTop: '50px',
    width: '100%',
  },
  logo: {
    marginLeft: '18%',
    [theme.breakpoints.down(BREAKPOINTS[1660])]: {
      marginLeft: '13%',
    },
    [theme.breakpoints.down(BREAKPOINTS[1380])]: {
      width: '244px',
      marginLeft: '5%',
    },
    [theme.breakpoints.down(BREAKPOINTS[1100])]: {
      width: '200px',
    },
    [theme.breakpoints.down(BREAKPOINTS[768])]: {
      width: '100px',
    },
  },
  links: {
    marginRight: '18%',
    [theme.breakpoints.up(BREAKPOINTS[1660])]: {
      marginRight: '13%',
    },
    [theme.breakpoints.down(BREAKPOINTS[1380])]: {
      marginRight: '5%',
    },
    [theme.breakpoints.down(BREAKPOINTS[768])]: {
      marginRight: '1%',
    },
    display: 'flex',
    alignItems: 'center',
  },
  link: {
    fontSize: '28px',
    lineHeight: '35px',
    fontWeight: 400,
    color: '#FED494',
    '&:first-of-type': {
      marginRight: '60px',
    },
    '&:nth-of-type(2)': {
      marginRight: '32px',
    },
    [theme.breakpoints.down(BREAKPOINTS[1380])]: {
      fontSize: '22px',
      lineHeight: '28px',
    },
    [theme.breakpoints.down(BREAKPOINTS[768])]: {
      marginRight: '8px !important',
      fontSize: '12px',
      lineHeight: '22px',
    },
  },
  select: {
    fontSize: '26px',
    fontWeight: 400,
    color: '#FED494',
    '& .MuiInputBase-input': {
      paddingTop: '0px !important',
      paddingBottom: '0px !important',
    },
    '& .MuiSelect-icon': {
      color: '#FED494',
    },
    '& .flag': {
      width: '35px',
      height: '35px',
      borderRadius: '20px',
      marginRight: '11px',
      [theme.breakpoints.down(BREAKPOINTS[1380])]: {
        width: '28px',
        height: '28px',
      },
      [theme.breakpoints.down(BREAKPOINTS[768])]: {
        width: '18px',
        height: '18px',
      },
    },
    [theme.breakpoints.down(BREAKPOINTS[1380])]: {
      fontSize: '21px',
    },
    [theme.breakpoints.down(BREAKPOINTS[768])]: {
      fontSize: '12px',
      lineHeight: '20px',
    },
  },
  icon: {
    color: '#FED494',
  },
  menuPaper: {
    top: '147px !important',
    background: '#17130D',
    border: '1px solid #DEA45C',
    borderRadius: '5px',
    color: '#FFFFFF',
    fontSize: '24px',
    '& .Mui-selected': {
      color: '#FED494',
    },
  },
}))

interface Props {
  locale: Locale
  onChangeLocale: (locale: Locale) => void
}

export default function Header(props: Props) {
  const { locale, onChangeLocale } = props
  const classes = useStyles()
  const { formatMessage: format } = useIntl()

  return (
    <header className={classes.header}>
      <img src={LogoImg} alt="logo" className={classes.logo} />
      <div className={classes.links}>
        <span className={classes.link}>{format(headerMessages.home)}</span>
        <span className={classes.link}>{format(headerMessages.register)}</span>
        <Select
          classes={{ selectMenu: classes.select, icon: classes.icon }}
          MenuProps={{
            classes: { paper: classes.menuPaper },
          }}
          disableUnderline={true}
          value={locale}
          onChange={(event) => onChangeLocale(event.target.value as Locale)}
          renderValue={(value) => {
            const lang = value as Locale
            const iconMap: Record<Locale, ReactNode> = {
              zh: <ZhFlagIcon className="flag" />,
              en: <UsFlagIcon className="flag" />,
              th: <ThFlagIcon className="flag" />,
            }
            return (
              <Box display="flex" alignItems="center">
                {iconMap[lang]} {format(headerMessages[lang as Locale])}
              </Box>
            )
          }}
        >
          <MenuItem value="th">ไทย</MenuItem>
          <MenuItem value="zh">中文</MenuItem>
          <MenuItem value="en">English</MenuItem>
        </Select>
      </div>
    </header>
  )
}
