import { makeStylesWithIndex } from 'common/hooks'
import ArrowBg from 'assets/img/arrow_bg.png'
import ArrowSmallBg from 'assets/img/small_arrow_bg.png'
import BtnSelectedBg from 'assets/img/btn_selected.png'
import CardBg from 'assets/img/card_bg.png'
import React, { useState } from 'react'
import { BREAKPOINTS } from 'common/constant'
import { Theme } from '@material-ui/core'

const useStyles = makeStylesWithIndex<
  Theme,
  { floatingPosition?: 'left' | 'right'; position?: 'flex-start' | 'flex-end' }
>((theme) => ({
  title: {
    position: 'relative',
    marginBottom: '43px',
    fontSize: '60px',
    color: '#78674B',
    width: '100%',
    maxWidth: '1340px',
    textAlign: 'center',
    backgroundImage: `url("${ArrowBg}")`,
    backgroundSize: 'contain',
    backgroundRepeat: 'no-repeat',
    backgroundPositionY: 'center',
    letterSpacing: '4px',
  },
  content: {
    position: 'relative',
    fontSize: '26px',
    lineHeight: '39px',
    color: '#D2D2D2',
    width: '100%',
    maxWidth: '1340px',
    textAlign: 'center',
    padding: '0px 64px',
    letterSpacing: '4px',
    '& img': {
      position: 'absolute',
      top: '10px',
      left: '0px',
    },
  },
  floatingImg: {
    animation: `$bounce ${theme.transitions.easing.easeInOut} 4s infinite`,
    position: 'absolute',
    top: '144px',
    left: (props) => (props.floatingPosition === 'left' ? '-40px' : undefined),
    right: (props) => (props.floatingPosition === 'right' ? '-40px' : undefined),
    zIndex: 5,
    [theme.breakpoints.down(BREAKPOINTS[1380])]: {
      left: (props) => (props.floatingPosition === 'left' ? '0px' : undefined),
      right: (props) => (props.floatingPosition === 'right' ? '0px' : undefined),
      top: '104px',
    },
  },
  '@keyframes bounce': {
    '0%': { transform: 'translateY(0)' },
    '50%': { transform: 'translateY(-30px)' },
    '100%': { transform: 'translateY(0)' },
  },
  selectableCard: {
    cursor: 'pointer',
    position: 'relative',
    maxWidth: '23%',
    '& .card-title': {
      fontSize: '28px',
      color: '#CAB063',
      textAlign: 'center',
      zIndex: 2,
      position: 'absolute',
      top: '16px',
      left: '0px',
      width: '100%',
      letterSpacing: '3px',
      fontWeight: 'bold',
    },
    '& > img': {
      width: '100%',
    },
    '& .selected': {
      position: 'absolute',
      top: '-20px',
      left: '0px',
      zIndex: 3,
    },
  },
  textCard: {
    position: 'relative',
    '& .card-container': {
      position: 'absolute',
      top: 0,
      left: 0,
      width: '100%',
    },
    '& .card-title': {
      textAlign: 'center',
      fontSize: '36px',
      color: '#EEDE9F',
      marginTop: '60px',
      letterSpacing: '4px',
    },
    '& .card-content': {
      fontSize: '22px',
      color: '#E7CFAB',
      padding: '40px 44px 0px',
    },
  },
  transitionCard: {
    width: '24%',
    maxWidth: '300px',
    maxHeight: '389px',
    '& > img': {
      width: '100%',
    },
    '& .transition-title': {
      fontSize: '24px',
      color: '#DBB688',
      position: 'relative',
      bottom: (props) => (props.position === 'flex-end' ? '32px' : '0px'),
    },
    '& .transition-content': {
      fontSize: '18px',
      color: '#E2E2E2',
      lineHeight: '26px',
      position: 'relative',
      bottom: (props) => (props.position === 'flex-end' ? '32px' : '0px'),
    },
  },
}))

export const ArrowTitle: React.FC<{ title: string; img: string }> = (props) => {
  const classes = useStyles({})

  return (
    <>
      <img src={props.img} alt="img" />
      <div className={classes.title}>
        <div style={{ width: '46%', marginLeft: '27%' }}>{props.title}</div>
      </div>
    </>
  )
}

export const ArrowContent: React.FC<{
  content: string
  img: string
  floatingPosition: 'left' | 'right'
}> = (props) => {
  const classes = useStyles({ floatingPosition: props.floatingPosition })

  return (
    <>
      <img className={classes.floatingImg} src={props.img} alt="red" />
      <div className={classes.content}>
        {props.content}
        <img style={{ width: '100%' }} src={ArrowSmallBg} alt="arrow_small" />
      </div>
    </>
  )
}

export const SelectableCard: React.FC<{
  selected: boolean
  img: string
  title: string
  onClick: () => void
}> = (props) => {
  const classes = useStyles({})

  return (
    <div className={classes.selectableCard} onClick={props.onClick}>
      {props.selected && (
        <img className="selected" src={BtnSelectedBg} alt="selected" draggable={false} />
      )}
      <div className="card-title">{props.title}</div>
      <img className="card-img" src={props.img} alt="img" draggable={false} />
    </div>
  )
}

export const TextCard: React.FC<{
  img: string
  title: string
  content: string
}> = (props) => {
  const classes = useStyles({})
  return (
    <div className={classes.textCard}>
      <img src={CardBg} alt="card" />
      <div className="card-container">
        <div className="card-title">
          <img src={props.img} alt="img" />
          <div>{props.title}</div>
        </div>
        <div className="card-content">{props.content}</div>
      </div>
    </div>
  )
}

export const TransitionCard: React.FC<{
  img: string
  title: string
  content: string
  position: 'flex-start' | 'flex-end'
}> = (props) => {
  const classes = useStyles({ position: props.position })
  const [hover, toggleHover] = useState(false)

  return (
    <div
      onMouseEnter={() => toggleHover(true)}
      onMouseLeave={() => toggleHover(false)}
      onTouchEnd={() => toggleHover(!hover)}
      className={classes.transitionCard}
      style={{ alignSelf: props.position }}
    >
      <img src={props.img} alt="img" />
      {hover ? (
        <div className="transition-content">{props.content}</div>
      ) : (
        <div className="transition-title">{props.title}</div>
      )}
    </div>
  )
}
