import OneImg from 'assets/img/1.png'
import HeartImg from 'assets/img/heart.png'
import RegisterGif from 'assets/img/register.gif'
import YearIncreaseImg from 'assets/img/year_increase.png'
import ActiveUserImg from 'assets/img/active_user.png'
import ProfitImg from 'assets/img/profit.png'
import { makeStylesWithIndex } from 'common/hooks'
import { useIntl } from 'react-intl'
import { sectionOneMessages } from 'translations/messages'
import { Box } from '@material-ui/core'

import { ArrowContent, ArrowTitle } from './SectionBase'

const useStyles = makeStylesWithIndex(() => ({
  sectionOne: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    position: 'relative',
  },
  registerNum: {
    color: '#D2D2D2',
    fontSize: '30px',
    '& .num': {
      fontSize: '108px',
      color: '#E3BD8D',
      marginTop: '61px',
      marginBottom: '140px',
    },
  },
  imgText: {
    marginTop: '47px',
    color: '#D2D2D2',
    fontSize: '30px',
    '& .number': {
      color: '#E6A821',
      fontSize: '40px',
      margin: '56px 0px 40px',
    },
  },
}))

const ImgText = (props: { img: any; number: string; content: string }) => {
  const classes = useStyles()
  return (
    <Box
      display="flex"
      justifyContent="center"
      alignItems="center"
      flexDirection="column"
      className={classes.imgText}
    >
      <img src={props.img} alt="img" />
      <div className="number">{props.number}</div>
      <div>{props.content}</div>
    </Box>
  )
}

export default function SectionOne(props: { scale: number }) {
  const classes = useStyles()
  const { formatMessage: format } = useIntl()

  return (
    <section
      className={classes.sectionOne}
      style={{
        transform: `scale(${props.scale})`,
      }}
    >
      <ArrowTitle title={format(sectionOneMessages.title)} img={OneImg} />
      <ArrowContent
        content={format(sectionOneMessages.content)}
        img={HeartImg}
        floatingPosition="left"
      />

      <Box display="flex" justifyContent="space-between" alignItems="flex-end" width="100%">
        <div className={classes.registerNum}>
          <div>{format(sectionOneMessages.registerNum)}</div>
          <div className="num">50,000,000+</div>
        </div>
        <img src={RegisterGif} alt="register" />
      </Box>

      <Box display="flex" justifyContent="space-between" width="100%">
        <ImgText
          img={YearIncreaseImg}
          number="500,000+"
          content={format(sectionOneMessages.yearIncreaseUser)}
        />
        <ImgText
          img={ActiveUserImg}
          number="20,000+"
          content={format(sectionOneMessages.dailyActiveUser)}
        />
        <ImgText
          img={ProfitImg}
          number="1,500,000,000+"
          content={format(sectionOneMessages.profit)}
        />
      </Box>
    </section>
  )
}
