import { styled } from '@material-ui/core'
import { FormattedMessage } from 'react-intl'
import { anchorMessages } from 'translations/messages'
import SlideImg from 'assets/img/icon_slide.png'

const Row = styled('div')({
  // position: 'relative',
  // top: '18%',
  color: '#E9CA8A',
  fontSize: '22px',
  fontWeight: 'bold',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  '& .arrow-img': {
    margin: '0px 23px',
  },
})

export default function AnchorRow(props: { scale: number }) {
  return (
    <Row
      style={{
        transform: `scale(${props.scale})`,
      }}
    >
      <FormattedMessage {...anchorMessages.mouseDown} />
      <img className="arrow-img" src={SlideImg} alt="arrow" />
      <FormattedMessage {...anchorMessages.scroll} />
    </Row>
  )
}
