import { makeStylesWithIndex } from 'common/hooks'
import { sectionThreeMessages } from 'translations/messages'
import ThreeImg from 'assets/img/3.png'
import DiamondImg from 'assets/img/diamond.png'
import ReputationImg from 'assets/img/reputation.png'
import FastImg from 'assets/img/fast.png'
import SecurityImg from 'assets/img/security.png'
import TitleBgImg from 'assets/img/title_bg.png'
import { ArrowTitle, ArrowContent, TextCard, TransitionCard } from './SectionBase'
import { useIntl } from 'react-intl'
import { Box } from '@material-ui/core'
import PrettyImg from 'assets/img/pretty.png'
import WineImg from 'assets/img/wine.png'
import ChipImg from 'assets/img/chip.png'
import BuildingImg from 'assets/img/building.png'

const useStyles = makeStylesWithIndex(() => ({
  sectionThree: {
    marginTop: '57px',
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    position: 'relative',
  },
  playerRights: {
    position: 'relative',
    textAlign: 'center',
    lineHeight: '165px',
    fontSize: '60px',
    color: '#E4BE8D',
    '& > img': {
      position: 'absolute',
      top: '0px',
      left: 'calc(50% - 285px)',
      zIndex: 1,
    },
    '& .title': {
      zIndex: 2,
      position: 'relative',
      letterSpacing: '4px',
    },
  },
}))

export default function SectionThree(props: { scale: number }) {
  const classes = useStyles()
  const { formatMessage: format } = useIntl()

  return (
    <section
      className={classes.sectionThree}
      style={{
        transform: `scale(${props.scale})`,
      }}
    >
      <ArrowTitle title={format(sectionThreeMessages.title)} img={ThreeImg} />
      <ArrowContent
        content={format(sectionThreeMessages.content)}
        img={DiamondImg}
        floatingPosition="left"
      />

      <Box display="flex" justifyContent="space-around" marginTop="70px" width="100%">
        <TextCard
          img={FastImg}
          title={format(sectionThreeMessages.fastTitle)}
          content={format(sectionThreeMessages.fastContent)}
        />
        <TextCard
          img={SecurityImg}
          title={format(sectionThreeMessages.securityTitle)}
          content={format(sectionThreeMessages.securityContent)}
        />
        <TextCard
          img={ReputationImg}
          title={format(sectionThreeMessages.reputationTitle)}
          content={format(sectionThreeMessages.reputationContent)}
        />
      </Box>

      <div className={classes.playerRights}>
        <img src={TitleBgImg} alt="title_bg" />
        <span className="title">{format(sectionThreeMessages.playerRights)}</span>
      </div>

      <Box display="flex" justifyContent="space-between" width="100%" height="475px">
        <TransitionCard
          img={PrettyImg}
          title={format(sectionThreeMessages.prettyTitle)}
          content={format(sectionThreeMessages.prettyContent)}
          position="flex-end"
        />
        <TransitionCard
          img={BuildingImg}
          title={format(sectionThreeMessages.buildingTitle)}
          content={format(sectionThreeMessages.buildingContent)}
          position="flex-start"
        />
        <TransitionCard
          img={WineImg}
          title={format(sectionThreeMessages.wineTitle)}
          content={format(sectionThreeMessages.wineContent)}
          position="flex-end"
        />
        <TransitionCard
          img={ChipImg}
          title={format(sectionThreeMessages.chipTitle)}
          content={format(sectionThreeMessages.chipContent)}
          position="flex-start"
        />
      </Box>
    </section>
  )
}
