import { CssBaseline, styled } from '@material-ui/core'
import BG from 'assets/img/bg.png'
import { Locale } from 'common/types'
import Header from 'components/Header'
import { useEffect, useRef, useState } from 'react'
import { IntlProvider } from 'react-intl'
import en from 'translations/en.json'
import zh from 'translations/zh.json'
import th from 'translations/th.json'
import AnchorRow from 'components/AnchorRow'
import SectionOne from 'components/SectionOne'
import { BREAKPOINTS } from 'common/constant'
import SectionTwo from 'components/SectionTwo'
import SectionThree from 'components/SectionThree'
import Footer from 'components/Footer'
// import useMediaQuery from '@material-ui/core/useMediaQuery';

const MESSAGES = {
  en,
  zh,
  th,
}

const Background = styled('div')(({ theme }) => ({
  position: 'relative',
  fontFamily: 'Microsoft YaHei',
  backgroundImage: `url("${BG}")`,
  backgroundSize: 'contain',
  backgroundRepeat: 'no-repeat',
  backgroundPositionX: 'center',
  [theme.breakpoints.up(BREAKPOINTS[1920])]: {
    backgroundSize: 'cover',
  },
}))

const Main = styled('main')(({ theme }) => ({
  width: '1440px',
  marginLeft: 'calc(50% - 720px)',
  paddingTop: '49%',
  [theme.breakpoints.down(BREAKPOINTS[1660])]: {
    width: '1320px',
    marginLeft: 'calc(50% - 660px)',
  },
  [theme.breakpoints.down(BREAKPOINTS[1380])]: {
    width: '1024px',
    marginLeft: 'calc(50% - 512px)',
  },
  [theme.breakpoints.down(1200)]: {
    paddingTop: '0px',
    position: 'absolute',
  },
}))

function App(props: { onHeightChanged: (height: string) => void }) {
  const mainRef = useRef<HTMLDivElement>(null)
  const footerRef = useRef<HTMLDivElement>(null)
  const [scale, setScale] = useState(window.innerWidth > 1200 ? 1 : window.innerWidth / 1200)
  const { onHeightChanged } = props

  useEffect(() => {
    // Above 1200px, we use width to control RWD
    // Below 1200px, we scale the whole component down
    const onResize = () => {
      if (!mainRef.current || window.innerWidth > 1200 || !footerRef.current) {
        // mainRef.current && mainRef.current.style.top = '0px'
        return
      }

      const newScale = window.innerWidth / 1200
      setScale(newScale)
      // Giving a find-tuned scale ratio for position top
      // IPAD: >= 768px
      // Mobile: < 768px
      let top
      if (window.innerWidth > 767) {
        top = 700 - (1200 - window.innerWidth) * (3.1 - newScale)
      } else {
        top = 750 - (1200 - window.innerWidth) * (2.85 - newScale)
      }
      const offset = 1000 * newScale
      const mainHeight = mainRef.current.clientHeight * newScale
      const footerHeight = footerRef.current.clientHeight

      onHeightChanged(`${mainHeight + offset + footerHeight}px`)
      mainRef.current.style.top = `${top}px`
      // 168: Header height
      footerRef.current.style.top = `${mainHeight + offset}px`
    }

    window.addEventListener('resize', onResize)
    setTimeout(() => onResize(), 500)

    return () => window.removeEventListener('resize', onResize)
  }, [onHeightChanged])

  return (
    <>
      <Main ref={mainRef} style={{ transform: `scale(${scale})` }}>
        <AnchorRow scale={1} />
        <SectionOne scale={1} />
        <SectionTwo scale={1} />
        <SectionThree scale={1} />
      </Main>
      <Footer footerRef={footerRef} />
    </>
  )
}

function AppWrapper() {
  const [locale, setLocale] = useState<Locale>('zh')
  const [height, setHeight] = useState('100%')

  return (
    <div style={{ background: 'rgb(14, 14, 16)' }}>
      <Background style={{ height }}>
        <CssBaseline />
        <IntlProvider messages={MESSAGES[locale]} locale={locale}>
          <Header locale={locale} onChangeLocale={setLocale} />
          <App onHeightChanged={setHeight} />
        </IntlProvider>
      </Background>
    </div>
  )
}

export default AppWrapper
